<template>
  <div class="my-container">
    <div class="my-layout">
      <!-- 左侧菜单栏 -->
      <div class="menu-sidebar">
        <el-menu
          :default-active="activeMenu"
          class="menu-list"
          @select="handleMenuSelect"
        >
          <el-menu-item index="profile">
            <i class="el-icon-user"></i>
            <span>个人资料</span>
          </el-menu-item>
          <el-menu-item index="security">
            <i class="el-icon-lock"></i>
            <span>密码安全</span>
          </el-menu-item>
          <el-menu-item index="binding" v-show="false">
            <i class="el-icon-connection"></i>
            <span>账号绑定</span>
          </el-menu-item>
          <el-menu-item index="certification">
            <i class="el-icon-document-checked"></i>
            <span>实名认证</span>
          </el-menu-item>
          <el-menu-item index="certificate" v-show="false">
            <i class="el-icon-trophy"></i>
            <span>我的证书</span>
          </el-menu-item>
          <el-menu-item index="message" v-show="false">
            <i class="el-icon-message"></i>
            <span>站内信息</span>
          </el-menu-item>
          <el-menu-item index="points" v-show="false">
            <i class="el-icon-coin"></i>
            <span>积分日志</span>
          </el-menu-item>
        </el-menu>
      </div>

      <!-- 右侧内容区 -->
      <div class="content-area">
        <!-- 个人资料 -->
        <el-card v-if="activeMenu === 'profile'" class="content-card">
          <div class="card-content">
            <el-form 
              ref="profileForm" 
              :model="userInfo" 
              :rules="rules"
              label-width="100px"
              class="profile-form"
            >
              <el-form-item label="用户编号">
                <el-input v-model="userInfo.userId" disabled></el-input>
              </el-form-item>
              
              <el-form-item label="登录账号">
                <el-input v-model="userInfo.userName" disabled></el-input>
              </el-form-item>

              <el-form-item label="姓名" prop="nickName">
                <el-input v-model="userInfo.nickName" placeholder="请输入姓名"></el-input>
              </el-form-item>

              <el-form-item label="手机号码">
                <el-input v-model="userInfo.phonenumber" disabled></el-input>
              </el-form-item>

              <el-form-item label="所属部门">
                <el-input v-model="userInfo.dept.deptName" disabled></el-input>
              </el-form-item>

              <el-form-item label="岗位">
                <el-input v-model="userInfo.postGroup" disabled></el-input>
              </el-form-item>

              <el-form-item label="角色">
                <el-input v-model="userInfo.roleGroup" disabled></el-input>
              </el-form-item>

              <el-form-item label="邮箱" prop="email">
                <el-input v-model="userInfo.email" placeholder="请输入邮箱"></el-input>
              </el-form-item>

              <el-form-item label="头像">
                <div class="avatar-uploader">
                  <img 
                    v-if="userInfo.avatar" 
                    :src="userInfo.avatar" 
                    class="avatar"
                  >
                  <div v-else class="avatar-placeholder">
                    <i class="el-icon-plus"></i>
                  </div>
                  <input 
                    type="file" 
                    accept="image/jpeg,image/png"
                    @change="handleAvatarUpload"
                    class="avatar-input"
                  >
                  <div class="upload-tip">请上传jpg或png格式图片</div>
                </div>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" @click="submitForm">保存修改</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-card>

        <!-- 密码安全 -->
        <el-card v-if="activeMenu === 'security'" class="content-card">
          <div class="card-content">
            <div class="card-header">
              <h3>修改密码</h3>
              <p class="tip">为了保证账号安全，请定期更换密码</p>
            </div>
            
            <el-form 
              ref="passwordForm" 
              :model="passwordForm" 
              :rules="passwordRules"
              label-width="100px"
              class="password-form"
            >
              <el-form-item label="原始密码" prop="oldPassword">
                <el-input
                  v-model="passwordForm.oldPassword"
                  type="password"
                  placeholder="请输入原始密码"
                  show-password
                ></el-input>
              </el-form-item>

              <el-form-item label="新的密码" prop="newPassword">
                <el-input
                  v-model="passwordForm.newPassword"
                  type="password"
                  placeholder="请输入新密码"
                  show-password
                ></el-input>
              </el-form-item>

              <el-form-item label="确认密码" prop="confirmPassword">
                <el-input
                  v-model="passwordForm.confirmPassword"
                  type="password"
                  placeholder="请再次输入新密码"
                  show-password
                ></el-input>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" @click="updatePassword">确认修改</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-card>

        <!-- 实名认证 -->
        <el-card v-if="activeMenu === 'certification'" class="content-card">
          <div class="card-content">
            <div class="card-header">
              <h3>实名认证</h3>
              <p class="tip">请填写真实的身份信息，认证后不可修改</p>
            </div>
            
            <el-form 
              ref="certificationForm" 
              :model="certificationForm" 
              :rules="certificationRules"
              label-width="100px"
              class="certification-form"
            >
              <el-form-item label="真实姓名" prop="realName">
                <el-input 
                  v-model="certificationForm.realName" 
                  placeholder="请输入真实姓名"
                ></el-input>
                <div class="form-tip error-tip">真实姓名不能为空！</div>
              </el-form-item>

              <el-form-item label="人脸照片" prop="faceImage">
                <div class="face-uploader">
                  <div 
                    class="face-preview"
                    :class="{ 'has-image': certificationForm.faceImage }"
                    @click="triggerUpload"
                  >
                    <img 
                      v-if="certificationForm.faceImage" 
                      :src="certificationForm.faceImage" 
                      class="preview-image"
                    >
                    <div v-else class="upload-placeholder">
                      <i class="el-icon-plus"></i>
                    </div>
                  </div>
                  <input 
                    ref="faceInput"
                    type="file" 
                    accept="image/jpeg,image/png"
                    @change="handleFaceUpload"
                    class="face-input"
                  >
                  <div class="upload-tips">
                    <p>用于人脸登录，需要保证正脸位于图片中央</p>
                    <p>图片质量会影响识别率</p>
                  </div>
                </div>
              </el-form-item>

              <el-form-item label="身份证号" prop="idCard">
                <el-input 
                  v-model="certificationForm.idCard" 
                  placeholder="请输入身份证号"
                ></el-input>
                <div class="form-tip error-tip">身份证号码格式不正确！</div>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" @click="submitCertification">提交认证</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
import { getUserProfile, updateUserProfile, uploadAvatar, updatePassword } from '@/api/user'
import logger from '@/utils/logger'

export default {
  name: 'MyCenter',
  data() {
    // 密码确认验证
    const validateConfirmPassword = (rule, value, callback) => {
      if (value !== this.passwordForm.newPassword) {
        callback(new Error('两次输入的密码不一致'))
      } else {
        callback()
      }
    }

    // 身份证号验证
    const validateIdCard = (rule, value, callback) => {
      const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
      if (!value) {
        callback(new Error('请输入身份证号'))
      } else if (!reg.test(value)) {
        callback(new Error('身份证号格式不正确'))
      } else {
        callback()
      }
    }
    
    return {
      activeMenu: 'profile',
      userInfo: {
        userId: '',
        userName: '',
        nickName: '',
        phonenumber: '',
        email: '',
        avatar: '',
        postGroup: '',
        roleGroup: '',
        dept: {
          deptName: ''
        }
      },
      rules: {
        nickName: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱格式', trigger: 'blur' }
        ]
      },
      passwordForm: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      passwordRules: {
        oldPassword: [
          { required: true, message: '请输入原始密码', trigger: 'blur' },
          { min: 6, message: '密码长度不能小于6位', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '请输入新密码', trigger: 'blur' },
          { min: 6, message: '密码长度不能小于6位', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '请再次输入新密码', trigger: 'blur' },
          { validator: validateConfirmPassword, trigger: 'blur' }
        ]
      },
      certificationForm: {
        realName: '',
        faceImage: '',
        idCard: ''
      },
      certificationRules: {
        realName: [
          { required: true, message: '请输入真实姓名', trigger: 'blur' },
          { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
        ],
        faceImage: [
          { required: true, message: '请上传人脸照片', trigger: 'change' }
        ],
        idCard: [
          { required: true, message: '请输入身份证号', trigger: 'blur' },
          { validator: validateIdCard, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 处理菜单选择
    handleMenuSelect(index) {
      this.activeMenu = index
    },
    // 获取个人资料
    async fetchUserProfile() {
      try {
        const res = await getUserProfile()
        if (res.code === 200) {
          const { user, postGroup, roleGroup } = res.data
          this.userInfo = {
            ...user,
            postGroup,
            roleGroup,
            dept: user.dept || { deptName: '' }
          }
        }
      } catch (error) {
        logger.error('获取个人资料失败:', error)
        this.$message.error('获取个人资料失败')
      }
    },

    // 提交个人资料
    async submitForm() {
      try {
        const res = await updateUserProfile({
          nickName: this.userInfo.nickName,
          email: this.userInfo.email,
          sex: this.userInfo.sex,
          phonenumber: this.userInfo.phonenumber
        })
        
        if (res.code === 200) {
          this.$message.success('保存成功')
          // 更新本地存储的用户信息
          const userInfo = JSON.parse(localStorage.getItem('userInfo') || '{}')
          localStorage.setItem('userInfo', JSON.stringify({
            ...userInfo,
            nickName: this.userInfo.nickName,
            email: this.userInfo.email,
            sex: this.userInfo.sex,
            phonenumber: this.userInfo.phonenumber
          }))
        }
      } catch (error) {
        logger.error('保存个人资料失败:', error)
        this.$message.error('保存失败')
      }
    },

    // 上传头像
    async handleAvatarUpload(e) {
      const file = e.target.files[0]
      if (!file) return

      // 验证文件类型
      const isImage = file.type.startsWith('image/')
      if (!isImage) {
        this.$message.error('请上传图片文件')
        return
      }

      // 验证文件大小（2MB）
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isLt2M) {
        this.$message.error('图片大小不能超过 2MB!')
        return
      }

      try {
        const formData = new FormData()
        formData.append('avatarfile', file)

        const res = await uploadAvatar(formData)
        if (res.code === 200) {
          this.userInfo.avatar = res.data.imgUrl
          this.$message.success('头像上传成功')
          
          // 更新本地存储的用户信息
          const userInfo = JSON.parse(localStorage.getItem('userInfo') || '{}')
          localStorage.setItem('userInfo', JSON.stringify({
            ...userInfo,
            avatar: res.data.imgUrl
          }))
        }
      } catch (error) {
        logger.error('上传头像失败:', error)
        this.$message.error('上传头像失败')
      }
    },

    // 更新密码
    async updatePassword() {
      this.$refs.passwordForm.validate(async valid => {
        if (!valid) return
        
        try {
          const res = await updatePassword(
            this.passwordForm.oldPassword,
            this.passwordForm.newPassword
          )
          
          if (res.code === 200) {
            this.$message.success('密码修改成功，请重新登录')
            // 清除登录信息
            localStorage.removeItem('token')
            localStorage.removeItem('userInfo')
            // 跳转到登录页
            this.$router.push('/login')
          }
        } catch (error) {
          logger.error('修改密码失败:', error)
          this.$message.error(error.response?.data?.msg || '修改密码失败')
        }
      })
    },

    // 触发文件选择
    triggerUpload() {
      this.$refs.faceInput.click()
    },

    // 处理人脸照片上传
    async handleFaceUpload(e) {
      const file = e.target.files[0]
      if (!file) return

      // 验证文件类型和大小
      const isImage = file.type.startsWith('image/')
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isImage) {
        this.$message.error('请上传图片文件!')
        return
      }
      if (!isLt2M) {
        this.$message.error('图片大小不能超过 2MB!')
        return
      }

      try {
        const formData = new FormData()
        formData.append('file', file)
        // TODO: 调用上传接口
        // const res = await uploadFace(formData)
        // if (res.code === 200) {
        //   this.certificationForm.faceImage = res.data.url
        //   this.$message.success('照片上传成功')
        // }
      } catch (error) {
        logger.error('上传照片失败:', error)
        this.$message.error('上传照片失败')
      }
    },

    // 提交认证
    submitCertification() {
      this.$refs.certificationForm.validate(async valid => {
        if (!valid) return

        try {
          // TODO: 调用认证接口
          // const res = await submitCertification(this.certificationForm)
          // if (res.code === 200) {
          //   this.$message.success('认证提交成功')
          // }
        } catch (error) {
          logger.error('认证提交失败:', error)
          this.$message.error('认证提交失败')
        }
      })
    }
  },
  created() {
    this.fetchUserProfile()
  }
}
</script>

<style scoped>
.my-container {
  padding: 20px;
  background: #f5f7fa;
  min-height: calc(100vh - 60px);
  display: flex;
  justify-content: center;
}

.my-layout {
  display: flex;
  gap: 10px;
  min-height: calc(100vh - 100px);
  max-width: 1400px;
  width: 100%;
}

.menu-sidebar {
  width: 200px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0,21,41,.08);
}

.menu-list {
  border-right: none;
}

:deep(.el-menu-item) {
  height: 50px;
  line-height: 50px;
  padding: 0 20px !important;
}

:deep(.el-menu-item.is-active) {
  background-color: #ecf5ff;
  color: #409EFF;
}

:deep(.el-menu-item:hover) {
  background-color: #f5f7fa;
}

:deep(.el-menu-item i) {
  margin-right: 10px;
  font-size: 18px;
}

.content-area {
  flex: 1;
  min-width: 0;
  padding: 0 20px;
  display: flex;
  justify-content: center;
}

.content-card {
  width: 100%;
  max-width: 1000px;
  min-height: 500px;
  margin-top: 0;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.05);
}

.card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
}

.card-header {
  width: 100%;
  max-width: 600px;
  margin-bottom: 40px;
  text-align: left;
  border-bottom: 1px solid #ebeef5;
  padding-bottom: 20px;
}

.card-header h3 {
  font-size: 20px;
  color: #303133;
  margin: 0 0 12px 0;
  font-weight: 500;
}

.profile-form,
.password-form,
.certification-form {
  width: 100%;
  max-width: 600px;
}

.tip {
  font-size: 14px;
  color: #909399;
  margin: 0;
}

.avatar-uploader {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0;
}

.avatar,
.avatar-placeholder {
  width: 120px;
  height: 120px;
  margin-bottom: 15px;
  transition: all 0.3s;
}

.avatar {
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.1);
}

.avatar-placeholder {
  border: 2px dashed #dcdfe6;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.avatar-placeholder:hover {
  border-color: #409EFF;
}

.avatar-placeholder i {
  font-size: 30px;
  color: #8c939d;
  transition: all 0.3s;
}

.avatar-placeholder:hover i {
  color: #409EFF;
}

.avatar-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
  opacity: 0;
  cursor: pointer;
}

.upload-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 8px;
}

:deep(.el-form-item) {
  margin-bottom: 28px;
}

:deep(.el-form-item__label) {
  font-weight: 500;
  color: #606266;
  padding-right: 20px;
}

:deep(.el-input__inner) {
  height: 42px;
  line-height: 42px;
  padding: 0 15px;
  border-radius: 4px;
}

:deep(.el-input.is-disabled .el-input__inner) {
  background-color: #f5f7fa;
  border-color: #e4e7ed;
  color: #909399;
}

:deep(.el-form-item:last-child) {
  margin-top: 40px;
  margin-bottom: 0;
  text-align: center;
}

:deep(.el-button--primary) {
  padding: 12px 35px;
  font-size: 14px;
  border-radius: 4px;
  font-weight: 500;
}

.certification-form {
  width: 100%;
  max-width: 500px;
}

.form-tip {
  font-size: 13px;
  margin-top: 6px;
  line-height: 1.4;
}

.error-tip {
  color: #f56c6c;
  display: flex;
  align-items: center;
}

.error-tip::before {
  content: "!";
  width: 16px;
  height: 16px;
  background: #fef0f0;
  border-radius: 50%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 6px;
  font-size: 12px;
  color: #f56c6c;
}

.face-uploader {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.face-preview {
  width: 240px;
  height: 240px;
  border: 2px dashed #dcdfe6;
  border-radius: 8px;
  margin-bottom: 15px;
  transition: all 0.3s;
}

.face-preview:hover {
  border-color: #409EFF;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,0.1);
}

.preview-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.upload-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.upload-placeholder i {
  font-size: 28px;
  color: #8c939d;
}

.face-input {
  display: none;
}

.upload-tips {
  background-color: #f5f7fa;
  padding: 12px 20px;
  border-radius: 4px;
  margin-top: 15px;
}

.upload-tips p {
  margin: 6px 0;
  color: #606266;
}
</style> 